import React, { Component } from 'react';
import Title from './title'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            list: Array(3).fill({
                title: '网站/WEB',
                childs: Array(3).fill({
                    name: 'SIMPLICITY',
                    img: '',
                    remark: 'by Doolin',
                    headImg: '',
                    price: '74',
                    sellCount: '1.5K'
                })
            }),
            list2: Array(8).fill({
                title: '公司展示',
                remark: 'EXHIBITION'
            })
        }
    }
    render() {
        return <div className="floor3 floor section-box">
            <Title title="应用分类" letter="APPLICATION" />
            {this.state.list.map((v, i) => <div key={i} className="list">
                <div>
                    <div className="list-title">{v.title}</div>
                    <div className="check-all-btn btn">查看更多</div>
                </div>
                <div className="item-list">
                    {v.childs.map((m, n) => <div key={n} className="item">
                        <img src={require(`../../assets/home/yy_0${n + 1}.png`)} alt="" />
                        <div className="name">{m.name}</div>
                        <div className="remark">{m.remark}</div>
                        <div className="sell-count">
                            <img src={require('../../assets/home/yy_hot.png')} alt="" />
                            {m.sellCount}</div>
                        <div className="data-line">
                            <img src={require(`../../assets/home/yy_user0${n + 1}.png`)} alt="" />
                            <div className="right">
                                <span className="price">{m.price}</span>
                                <span className="img-box btn">
                                    <img src={require('../../assets/home/yy_buy.png')} alt="" />
                                </span>
                            </div>
                        </div>
                    </div>)}
                </div>
            </div>)}
            <div className="list2">
                {this.state.list2.map((v, i) => <div key={i} className="item">
                    <img src={require(`../../assets/home/yy_page0${i + 1}.png`)} alt="" />
                    <div className="list-content">
                        <div>{v.title}</div>
                        <div className="remark">{v.remark}</div>
                    </div>
                </div>)}
            </div>
            <div className="btn-check-all btn">查看全部</div>
        </div>
    }
}