import React, { Component } from 'react';
import './header.less';
class App extends Component {
    static defaultProps = {
        className: ''
    }

    constructor(props) {
        super(props)
        this.state = {
            current: 0,
            topNav: [
                {
                    txt: '首页',
                    url: '#home'
                }, {
                    txt: '应用',
                    url: '#yingyong'
                }, {
                    txt: '服务',
                    url: '#fuwu'
                }, {
                    txt: '案例',
                    url: '#anli'
                }, {
                    txt: '资讯',
                    url: '#zixun'
                }, {
                    txt: '找物',
                    url: '#zhaowu'
                },
            ]
        }
    }

    render() {
        return <div className={`header-box ${this.props.className}`}>
            <div className='left'>
                <img className='logo' src={require('../../assets/logo.png')} alt="logo" />
                {this.state.topNav.map((v, i) => <a key={i} className={`nav-item ${v.txt === '首页' ? '' : 'arrow-right'} ${i === this.state.current ? 'current' : ''}`} onClick={this.clickItem.bind(this, i)} href={v.url}>{v.txt}</a>
                )}
            </div>
            <div className='right'>
                <div className='cart-box'>
                    <img src={require('../../assets/top_shop.png')} alt="cart" className='cart' />
                </div>
                <div className='login'>登录/注册</div>
            </div>
        </div>
    }
    clickItem(current) {
        this.setState({
            current
        })
    }
}
export default App