import React, { Component } from 'react';
export default class App extends Component {
    static defaultProps = {
        titleColor: ''
    }
    render() {
        return <div className="module-title">
            <div className="title" style={{ color: this.props.titleColor }}>{this.props.title}</div>
            <div className="letter">{this.props.letter}</div>
        </div >
    }
}