import React, { Component } from 'react';
import { Carousel } from 'antd';
import { floor5 } from '../../data/zhibo'
import Ex from '../../components/wxappExample'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...floor5
        }
    }
    render() {
        return <div className="floor4 floor">
            <div className="section-box">
                <div className="title">
                    {this.state.title[0]}<span>{this.state.title[1]}</span>
                </div>
                <div className="description">{this.state.description}</div>
                <Carousel draggable autoplaySpeed={5000} autoplay dots={{ className: 'dot' }}>
                    {this.state.list.map((v, i) => <div className="carousel" key={i}>
                        {this.state.list.map((m, n) => <img key={i + '-' + n} src={require(`../../assets/anli_0${n + 1}.png`)} alt="" />)}
                    </div>)}
                </Carousel>
                <Ex />
                <span className="service btn" onClick={() => { this.props.showQQ() }}>了解更多</span>
            </div>
        </div>
    }
}