import React, { Component } from 'react';
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            current: 0,
            navList: [
                '小程序商城',
                '直播系统',
                '多商户系统',
                '知识付费系统',
                '水果生鲜系统',
                '积分会员系统',
                '快速建站'
            ],
            content: [
                '直播',
                '会员管理',
                '积分管理',
                '营销功能',
            ],
            content2: [
                '课程管理',
                '主播入驻',
                '多商户入驻',
            ]
        }
    }
    render() {
        return <div className="floor1">
            <img src={require('../../assets/home/banner_bg01.png')} alt="" />
            <div className="main section-box">
                <div className="left">
                    <div className='description'>多渠道打造您的专属平台</div>
                    {this.state.navList.map((v, i) => <div className={`nav-item btn ${i === this.state.current ? 'current' : ''}`} key={i} onClick={this.clickItem.bind(this, i)}>
                        <img src={require(`../../assets/home/banner_icon0${i + 1}.png`)} alt="" />{v}
                    </div>)}
                </div>
                <div className="center">
                    <div className='title'>
                        找物
                        <span>小程序商城</span>
                    </div>
                    <div className="description">助力企业迈向电商新时代</div>
                    <div className="content">{
                        this.state.content.join(' | ')
                    }
                    </div>
                    <div className="content">{
                        this.state.content2.join(' | ')
                    }
                    </div>
                    <span className="zixun-btn btn">
                        免费咨询
                    </span>
                </div>
            </div>
        </div>
    }
    clickItem(current) {
        this.setState({
            current
        })
    }
}