import React, { Component } from 'react';
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            list: [
                {
                    title: '小程序',
                    txt: '需要改名字吗？'
                }, {
                    title: '分销提现',
                    txt: '如何快速实现'
                }, {
                    title: '店铺[装修]',
                    txt: '快速引入优质流量'
                }, {
                    title: '网站快速建设',
                    txt: ''
                },
            ]
        }
    }
    render() {
        return <div className="floor2 section-box">
            {this.state.list.map((v, i) => <div className='item' key={i}>
                <img src={require(`../../assets/home/yxfa_0${i + 1}.png`)} alt="" />
                <div>
                    <div>{v.title}</div>
                    <div>{v.txt}</div>
                </div>
            </div>)}
        </div>
    }
}