import React, { Component } from 'react';
import Title from './title'
import { Carousel } from 'antd';
import Ex from '../../components/wxappExample'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
        }
    }
    render() {
        return <div className="floor7 floor">
            <Title title="案例展示" letter="CASE HIGHLIGHTS" />
            <div className="bgimg-box">
                <Carousel draggable autoplaySpeed={5000} autoplay dots={{ className: 'section-box dot' }}>
                    {Array(4).fill('').map((v, i) => <div className="carousel" key={i}>
                        {Array(4).fill('').map((m, n) => <img key={i + '-' + n} src={require(`../../assets/home/anli_0${n + 1}.png`)} alt="" />)}
                    </div>)}
                </Carousel>
                <img src={require('../../assets/home/anli_bg.png')} alt="" />
            </div>
            <div className="section-box">
                <Ex />
            </div>
        </div>
    }
}