import React, { Component } from 'react';
import Title from './title'
import { floor4 } from '../../data/zhibo'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            floor4,
            list: [
                {
                    name: '代开通小程序',
                    remark: '提供企业营业执照、法人身份证、手机号、邮箱等材料。'
                }, {
                    name: '代申请支付',
                    remark: '通过公众号、小程序申请、微信支付需要满三个月，并且连续正常交易一个月可以开通微信支付到零钱功能，做分销、供应商提现的用户需要注意。'
                }, {
                    name: '小程序发布',
                    remark: '独立申请或通过公众号申请小程序，管理员授权，支付双向绑定，配置小程序上传小程序代码，审核，发布。'
                },
            ]
        }
    }
    render() {
        return <div className="floor6 floor section-box">
            <Title title="服务功能" letter="SERVICE FUNCTION" />
            <div className="more-list">
                {
                    this.state.floor4.moreList.map((v, i) => <span className={i % 7 % 2 === 0 ? 'item-bg' : ''} key={i}>{v}</span>)
                }
            </div>
            <div className="list">
                {
                    this.state.list.map((v, i) => <span key={i}>
                        <img src={require(`../../assets/home/qudao_0${7+i}.png`)} alt="" />
                        <div>{v.name}</div>
                        <div>{v.remark}</div>
                    </span>)
                }
            </div>
            <div className="btn-check-all btn">查看全部</div>
        </div>
    }
}