import React, { Component } from 'react';
import Title from './title'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
        }
    }
    render() {
        return <div className="floor5">
            <div className="floor section-box">
                <Title title="运营渠道" letter="OPERATION CHANNELS" />
                <div className="list">
                    {Array(12).fill('').map((v, i) => <div className="item" key={i}>
                        <img src={require(`../../assets/home/qudao_${i<9?'0'+(i+1):'01'}.png`)} alt=""/>
                    </div>)}
                </div>
            </div>
        </div>
    }
}