import React, { Component } from 'react';
import './index.less';
import Header from '../../components/header/header'
import Floor1 from './floor1'
import Floor2 from './floor2'
import Floor3 from './floor3'
import Floor4 from './floor4'
import News from '../../components/news'
import Footer from '../../components/footer/footer'
import FixRight from '../../components/fixedRight'
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    render() {
        document.title = '直播系统-小程序直播系统-小程序直播-网上直播系统'
        return <div className="page-zhibo">
            <Header />
            <a name="home"></a>
            <Floor1 showQQ={this.showQQ.bind(this)} />
            <a name="yingyong"></a>
            <Floor2 showQQ={this.showQQ.bind(this)} />
            <a name="fuwu"></a>
            <Floor3 showQQ={this.showQQ.bind(this)} />
            <a name="anli"></a>
            <Floor4 showQQ={this.showQQ.bind(this)} />
            <a name="zixun"></a>
            <News />
            <a name="zhaowu"></a>
            <Footer />
            <FixRight onRef={(ref) => this.fixRight = ref} />
        </div>
    }
    showQQ() {
        this.fixRight.clickItem(0)
    }
}
export default App