import React, { Component } from 'react';
import zhibo from "../zhibo/index"
import zhiboM from "../../mobile-pages/zhibo/index"
import home from "../home/index"
import './index.less'
import {
    BrowserRouter as Router,
    Switch,
    Redirect,
    Route,
} from "react-router-dom";
class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            env: 'Pc',
            isPc: 1,
            route: []
        }
    }
    componentDidMount() {
        this.isPc()
    }
    isPc() {
        var sUserAgent = navigator.userAgent.toLowerCase();
        var bIsIpad = sUserAgent.match(/ipad/i);//判断是否为iPad
        var bIsIphoneOs = sUserAgent.match(/iphone os/i);//判断是否为iPhone用户
        var bIsMidp = sUserAgent.match(/midp/i);
        var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i);
        var bIsUc = sUserAgent.match(/ucweb/i);
        var bIsAndroid = sUserAgent.match(/android/i);
        var bIsCE = sUserAgent.match(/windows ce/i);
        var bIsWM = sUserAgent.match(/windows mobile/i);
        let env = 'Phone',
            isPc = 0
        if (!(bIsIpad || bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM)) {
            env = 'Pc'
            isPc = 1
        }
        this.setState({
            env,
            isPc
        })
        return env
    }
    render() {
        console.log(this.state)
        // return <div className={this.state.env === 'Pc' ? "pc-box" : "phone-box"}>
        //     <Router>
        //         <Switch>
        //             <Route path="/" exact component={this.state.env === 'Pc' ? zhibo : zhiboM} />
        //             <Route path="/Home" component={home} />
        //             {/* <Redirect exact from="/*" to="/" /> */}
        //         </Switch>
        //     </Router>
        // </div>
        let doms = [
            <div className='phone-box'>
                <Router>
                    <Switch>
                        <Route path="/" component={zhiboM} />
                        {/* <Redirect from="/*" to="/" /> */}
                    </Switch>
                </Router>
            </div>,
            <div className='pc-box'>
                <Router>
                    <Switch>
                        <Route path="/" component={zhibo} />
                        <Route path="/home" component={home} />
                        {/* <Redirect from="/*" to="/" /> */}
                    </Switch>
                </Router>
            </div>
        ]
        return doms[this.state.isPc]
    }
}
export default App


function Pc() {
    return <div className='pc-box'>
        <Router>
            <Switch>
                <Route exact path="/home" component={home} />
                <Route path="/" component={zhibo} />
                {/* <Redirect from="/*" to="/" /> */}
            </Switch>
        </Router>
    </div>;
}

function Phone() {
    return <div className='phone-box'>
        <Router>
            <Switch>
                <Route exact path="/" component={zhiboM} />
                {/* <Redirect from="/*" to="/" /> */}
            </Switch>
        </Router>
    </div>;
}