import React, { Component } from 'react';
import { floor5 } from '../data/zhibo'
export default class App extends Component {
    constructor(props) {
        super(props)
        this.state = {
            ...floor5
        }
    }
    render() {
        return <div className="service-list-c">
            {this.state.serviceList.map((v, i) => <span key={i}>
                <img src={require(`../assets/anli_${5 + i < 10 ? '0' + (5 + i) : 5 + i}.png`)} alt="" />
                <div>
                    <div>经营类目：{v.type}</div>
                    <div className="item-title">{v.title}</div>
                    <div className="item-txt">{v.txt}
                    </div>
                    <div className="link-more btn">了解详情</div>
                </div>
            </span>)}
        </div>
    }
}